<template>
  <div class="register-content">
    <myHeader></myHeader>
    <el-form :model="registerForm" :rules="rules" ref="registerForm" label-width="100px" class="register-form">
      <div class="register-font"></div>

      <el-form-item label="" prop="cname" required>
        <el-input v-model="registerForm.cname"></el-input>
      </el-form-item>

      <el-form-item label="" prop="phone" required>
        <el-input v-model="registerForm.phone"></el-input>
      </el-form-item>


      <el-form-item label="" prop="email" required>
        <el-input v-model="registerForm.email"></el-input>
      </el-form-item>

      <el-form-item label="" prop="pass" required>
        <el-input type="password" v-model="registerForm.pass" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="checkPass" required>
        <el-input type="password" v-model="registerForm.checkPass" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item label="" prop="residenceAddress" required>
        <el-select v-model="registerForm.residenceAddress" placeholder="" value="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" required>
        <el-col :span="11">
          <el-form-item prop="birthday">
            <el-date-picker type="date" placeholder="" v-model="registerForm.date"
                            style="width: 100%;"></el-date-picker>
          </el-form-item>
        </el-col>
        <!--<el-col class="line" :span="2">-</el-col>-->
        <!--<el-col :span="11">-->
          <!--<el-form-item prop="date2">-->
            <!--<el-time-picker type="fixed-time" placeholder="" v-model="registerForm.date2"-->
                            <!--style="width: 100%;"></el-time-picker>-->
          <!--</el-form-item>-->
        <!--</el-col>-->
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="filterFinancing" filterable placeholder=""  class="filter">
          <el-option
            v-for="(financing,index) in filterCondition.financingList"
            :key="index"
            :value="financing.financing">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="filterCompanyScale" filterable placeholder=""  class="filter">
          <el-option
            v-for="(companyScale,index) in filterCondition.companyScaleList"
            :key="index"
            :value="companyScale.companyScale">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="gender" required>
        <el-radio-group v-model="registerForm.gender">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('registerForm')"></el-button>
        <el-button @click="resetForm('registerForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import myHeader from '@/components/ZPHeader'
  export default {
    name: 'EnterpriseRegister',
    components: {myHeader},
    data () {
      // 
      var checkPhone = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''))
        } else {
          const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
          console.log(reg.test(value))
          if (reg.test(value)) {
            callback()
          } else {
            return callback(new Error(''))
          }
        }
      }
      var checkEmail = (ruler, value, callbacks) =>{
        const emailReg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/
        if(emailReg.test(value)){
          callbacks()
        }else{
          return callbacks(new Error(''))
        }
      }
      return {
        registerForm: {
          cname: '',
          phone: '',
          email: '',
          pass: '',
          checkPass:'',
          residenceAddress: '',
          filterCondition:
            {
              financingList : [
                {
                  financing: ''
                },
                {
                  financing: ''
                },
                {
                  financing: ''
                },
                {
                  financing: 'A'
                },
                {
                  financing: 'A'
                },
                {
                  financing: 'C'
                },
                {
                  financing: 'D'
                },
                {
                  financing: ''
                },
                {
                  financing: ''
                }
              ],
              companyScaleList: [
                {
                  companyScale: ''
                },
                {
                  companyScale: '0-20'
                },
                {
                  companyScale: '20-99'
                },
                {
                  companyScale: '100-499'
                },
                {
                  companyScale: '500-999'
                },
                {
                  companyScale: '500-999'
                },
                {
                  companyScale: '1000-9999'
                },
                {
                  companyScale: '10000'
                }
              ]
            },
        },
        rules: {
          cname: [
            { required: true, message: '', trigger: 'blur' }
          ],
          residenceAddress: [
            { required: true, message: '', trigger: 'change' }
          ],
          birthday: [
            { type: 'date', required: true, message: '', trigger: 'change' },
            { required: true, message: '', trigger: 'change' }
          ],
          pass: [
            { required: true, message: '', trigger: 'blur' }
          ],
          checkPass: [
            { required: true, message: '', trigger: 'blur' }
          ],
          phone:[
            { required: true, message: '', trigger: 'blur' },
            {validator: checkPhone, trigger: 'blur'},
          ],
          email: [
            {validator: checkEmail, trigger:'blur'},
            { required: true, message: '', trigger: 'blur' }
          ],
          gender: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            this.$message.error('')
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>
<style scoped>
  .register-form{
    margin: 100px;
    background: #FFF;
    padding: 50px;
    box-shadow: 3px 0 10px 5px #9bceeb;
    border-radius: 20px;
  }
  .register-font{
    font-size: 30px;
    font-weight: 900;
    display: flex;
    justify-content: center;
    margin: 20px 0;
    color: #999;
    /*linear-gradient(<angle> | to <side-or-corner> <color-stop>[, <color-stop>]+));*/
    /*background: linear-gradient( to top right, #f6f5f0, #fefefd);*/
    /*background: radial-gradient(circle, #3ddccc, #59a0ff);*/
  }
</style>



